'use strict';

// MODULES //

import shape2strides from '@stdlib/ndarray-base-shape2strides';
import strides2offset from '@stdlib/ndarray-base-strides2offset';
import resolveStr from '@stdlib/ndarray-base-dtype-resolve-str';
import ndarray from '@stdlib/ndarray-base-ctor';
import numel from '@stdlib/ndarray-base-numel';
import emptyArray from '@stdlib/array-empty';
import allocUnsafe from '@stdlib/buffer-alloc-unsafe';


// MAIN //

/**
* Creates an uninitialized ndarray having a specified shape and data type.
*
* @param {*} dtype - data type
* @param {NonNegativeIntegerArray} shape - array shape
* @param {string} order - array order
* @throws {TypeError} first argument must be a recognized data type
* @returns {ndarray} ndarray
*
* @example
* import getShape from '@stdlib/ndarray-shape';
* import getDType from '@stdlib/ndarray-dtype';
*
* var arr = empty( 'float32', [ 2, 2 ], 'row-major' );
* // returns <ndarray>
*
* var sh = getShape( arr );
* // returns [ 2, 2 ]
*
* var dt = String( getDType( arr ) );
* // returns 'float32'
*/
function empty( dtype, shape, order ) {
	var ndims;
	var buf;
	var len;
	var st;
	var dt;

	ndims = shape.length;
	if ( ndims > 0 ) {
		len = numel( shape );
		st = shape2strides( shape, order );
	} else {
		// For 0-dimensional arrays, the buffer should contain a single element...
		len = 1;
		st = [ 0 ];
	}
	dt = resolveStr( dtype );
	if ( dt === 'binary' ) {
		buf = allocUnsafe( len );
	} else {
		buf = emptyArray( len, dt );
	}
	// FIXME: add support for struct dtypes. Will need to do something similar to `array/empty` in so far as allocating an uninitialized array buffer, performing byte alignment, and creating a StructArray view atop the buffer.

	return new ndarray( dtype, buf, shape, st, strides2offset( shape, st ), order ); // eslint-disable-line max-len
}


// EXPORTS //

export default empty;
